/*=== VARIABLES ===*/
$color1:#f26f29;
$color2:#ffba06;
$color3:#a9d63b;
$color4:#6ab3d1;
$color5:#907ee2;
$color6:#e868a7;
$color7: rgba(255, 255, 255, .7); 

$primary-color:$color1;
$secondary-color:#253139;
$white-color: #ffffff;
$black-color: #000000;
$text-color:#848484;
$title-color: #222222;
$border-color: #e5e5e5;

@function tint($primary-color, $percentage) {
  @return mix(white, $primary-color, $percentage);
}
