/*================= 8. EXTRA ==================*/
/*================= 8.1 ERROR SECTION ==================*/
.error-content{
	display: block;
	@include tablet{
		display: flex;
		justify-content: center;
	}
}
.error-image{
	margin: 0 auto 80px auto;
	width: 270px;
	height: 270px;
	@include tablet{
		margin: 0 80px 0 0;
		width: 310px;
		height: 310px;
	}
}
.error-info{
	display: block;
	width: 100%;
	@include tablet{
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		width: 50%;
	}
	h2{
		color: $title-color;
		font-family: 'Open Sans', sans-serif;
		font-size: 48px;
		font-weight: 400;
		margin: 0 0 20px 0;
		@include tablet{
			margin: 0;
		}
	}
	p{
		font-size: 28px;
		line-height: 35px;
		margin: 0 0 20px 0;
		@include tablet{
			margin: 0;
		}
	}
	.btn-main{
		align-self: flex-start;
		padding: 0 40px;
	}
}
.search-align{
	display: flex;
	justify-content: center;
}
.search-error{
	border: 1px solid $border-color;
	border-radius: 6px;
	margin: 0 0 20px 0;
	@include tablet{
		margin: 0;
	}
	.form-control{
		box-shadow: none;
		font-size: 14px;
		padding: 0 25px;
	}
	.input-group-addon{
		background-color: transparent;
		border: none;
		width: 50px;
		text-align: center;
		color: $primary-color;
		font-size: 14px;
	}
}

/*================= 8.2 TEACHERS PAGE SECTION ==================*/
.teachers-page{
	padding: 100px 0 85px 0;
}
.teachers-space{
	margin: 15px 0;
}
.teacher-photo{
	box-shadow: 6px 6px 7px rgba(#273143, .13);
	border-radius: 6px;
	margin: 0 0 25px 0;
	@include tablet{
		margin: 0;
	}
	img{
		border-radius: 6px;
		width: 100%;
	}
}
.team-info{
	margin: 0 0 60px 0;
	h2{
		font-size: 28px;
		font-weight: 400;
		text-transform: capitalize;
		margin: 0 0 15px 0;
		color: $title-color;
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
	}
	h5{
		font-size: 14px;
		font-weight: 400;
		margin: 0 0 30px 0;
		color: $primary-color;
		text-transform: capitalize;
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
	}
	p{
		margin: 0 0 30px 0;
	}
}

.list-address-item{
	margin: 0 0 40px 0;
	li{
		font-size: 14px;
		line-height: 26px;
		padding: 7px 0;
		span{
			width: 63px;
			color: #222222;
			font-size: 18px;
			text-align: left;
			line-height: 26px;
			text-transform: capitalize;
			font-family: 'Londrina Solid', cursive;
			font-weight: 500;
		}
