}
.list-social-color{
	li{
		a{
			width: 35px;
			height: 35px;
			display: block;
			border-radius: 100%;
			color: $white-color;
			text-align: center;
			line-height: 35px;
		}
	}
}

.skills-title{
	h3{
		font-size: 28px;
		color: $primary-color;
		display: flex;
		align-items: center;
		margin: 0 0 25px 0;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-blue.png);
		}
