/*================= 8.3 COMMING SOON SECTION ==================*/
.comming-section{
	padding: 0;
	height: 100vh;
}
.comming-wrapper{
	display: flex;
	height: 100vh;
}
.comming-content{
	margin: auto;
	text-align: center;
}
.comming-logo{
	margin-bottom: 30px;
}
.comming-info{
	h1{
		font-family: 'Open Sans', sans-serif;
		color: $title-color;
		font-size: 42px;
		font-weight: 400px;
		letter-spacing: 0;
		text-transform: capitalize;
	}
	p{
		font-size: 22px;
	}
}
.count-down{
	padding: 0 0 35px 0;
	#simple_timer{
		width: 100%;
		margin: 0 auto;
		text-align: center;
		font-family: 'Londrina Solid', cursive;
		.table-cell{
			display: inline-block;
			@include mobile {
				margin: 0 10px;
			}
			@include tablet {
				margin: 0 10px;
			}
			@include desktops {
				margin: 0 10px;
			}
			@include large-desktops {
				margin: 0 12px;
			}
			.tab-val{
				display: block;
				margin-bottom: 20px;
				@include border-radius(7px);
				background-color: $white-color;
				box-shadow: 15px 15px 15px rgba(#273143, .13);
				width: 65px;
				height: 65px;
				line-height: 65px;
				font-size: 25px;
				border-radius: 100%;
				color: $color2;
				@include tablet {
					width: 85px;
					height: 85px;
					line-height: 85px;
					font-size: 35px;
				}
				@include desktops {
					width: 85px;
					height: 85px;
					line-height: 85px;
					font-size: 35px;
				}
				@include large-desktops {
					width: 120px;
					height: 120px;
					line-height: 120px;
					font-size: 60px;
				}
			}
			.tab-metr{
				display: none;
			}
			&.hour{
				.tab-val{
					color: $color3;
				}
			}
			&.minute{
				.tab-val{
					color: $primary-color;
				}
			}
			&.second{
				.tab-val{
					color: $color4;
				}
			}
		}
	}
}
.comming-input{
  display: flex;
  border: 1px solid $border-color;
  border-radius: 10px;
  height: auto;
  width: 75%;
  margin: 0 auto 25px auto;
  .form-control{
  	box-shadow: none;
  	border-radius: 10px;
  	height: 50px;
  	padding: 0 25px;
  }
  .input-group-addon{
  	border-radius: 0 10px 10px 0;
  	border: none;
  	background-color: transparent;
  	padding: 0;
  	height: 50px;
  	width: 50px;
  	color: $primary-color;
  }
}
/*================= 8.4 CONTACT SECTION ==================*/
