/*================= 8.6 PHOTO GALLERY SECTION ==================*/
.filter-container{
	padding: 0 20px 30px 20px;
}
.isotopeFilters{
	ul.filter{
		> li {
			padding: 6px 2px;
			@include tablet{
				padding: 0 2px;
			}
			a{
				padding: 0 25px;
				height:  40px;
				font-size: 14px;
				line-height: 40px;
				color: #222222;
				text-transform: uppercase;
				font-weight: 600;
				display: inline-block;
				box-shadow: 6px 6px 7px rgba(#273143, .13);
				background-color: #f8f8f8;
				border-radius: 4px;
			}
			&.active{
				a{
					background-color: $color1;
					color: $white-color;
				}
			}
		}
	}
}
.fancybox-lock{
	.fancybox-overlay{
		background-color: rgba($black-color, .75);
	}
}
.isotopeSelector{
	img{
		width: 100%;
	}
}
.isotopeSelector{
	figure{
		position: relative;
		margin-bottom: 30px;
		@include border-radius(4px);
	}
	.overlay-background{
		@include border-radius(4px);
		@include transition(all 0.3s ease-in-out);
	}
	.overlay{
		width: 100%;
		height: 100%;
		background-color: rgba($black-color, .5);
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;
		@include transition(all 0.3s ease-in-out);
		border-radius: 6px;
	}
	&:hover{
		.overlay{
			@include opacity(1);
			.overlayInfo{
				@include transform (translate(-50%, -50%));
			}
		}
		h4{
			color: $color3;
		}
	}
}
