/*================= 8.10 CART SECTION ==================*/
.cartInfo{
	margin: 0 0 50px 0;
	box-shadow: 20px 20px 15px rgba(#273143, .12);
	border-radius: 7px;
	.table-responsive{
		.table{
			border-collapse: separate;
			margin: 0;
			thead{
				tr{
					border-left: 1px solid $color1;
					border-right: 1px solid $color1;
					th{
						color: $white-color;
						padding: 15px 8px 15px 30px;
						border-bottom: none;
						font-weight: 600;
						text-transform: uppercase;
						&:first-child{
							border-radius: 7px 0 0 0;
						}
						&:last-child{
							border-radius: 0 7px 0 0;
						}
					}
				}
			}
			tbody{
				tr{
					td{
						vertical-align: middle;
						color: $title-color;
						border-top: none;
						border-bottom: 1px solid $border-color;
						@include mobile-xs{
							padding: 10px;
						}
						@include desktops{
							padding: 10px;
						}
						@include large-desktops{
							padding: 20px 8px 20px 30px;
						}
						&:first-child{
							border-left: 1px solid $border-color;
						}
						&:last-child{
							border-right: 1px solid $border-color;
							font-weight: 600;
						}
						&:nth-child(2){
							font-weight: 600;
						}
						.close{
							display: inline-block;
							float: left;
							margin: 17px 30px 0 0;
							opacity: 1;
							span{
								width: 30px;
								height: 30px;
								line-height: 30px;
								display: block;
								background-color: transparent;
								color: $primary-color;
								@include border-radius(100%);
							}
						}
						input {
							width: 80px;
							height: 45px;
							text-align: center;
							line-height: 45px;
							color: #222222;
							border: 1px solid $border-color;
							border-radius: 7px;
						}
						&:last-child{
							padding-right: 30px;
						}
					}
					&:last-child{
						td{
							&:first-child{
								border-radius: 0 0 0 7px;
							}
							&:last-child{
								border-radius: 0 0 7px 7px;
								&.btnPart{
									border-radius: 0 0 7px 7px;
								}
							}
						}
					}
				}
			}
			.input-group{
				.form-control{
					width: 257px;
					height: 45px;
					text-align: left;
					margin-right: 10px;
				}
				.input-group-addon{
					width: auto;
					border-radius: 7px;
				}
			}
		}
	}
	&.cartTotal{
		.table{
			tbody{
				tr{
					td{
						&:last-child{
							padding: 25px;
							text-align: right;
						}
					}
				}
			}
		}
	}
}
