/*================= 7. Blog ==================*/
.blog-section{
	padding: 85px 0 0 0;
}
.panel-sidebar{
	border: none;
	border-radius: 6px;
	margin: 0 0 50px 0;
	box-shadow: none;
	.panel-heading{
		background-color: transparent;
		border: none;
		border-radius: 6px 6px 0 0;
		padding: 0px 0 30px 0;
		h3{
			font-size: 24px;
			color: $primary-color;
			display: flex;
			align-items: center;
			margin: 0;
			.title-about{
				padding: 0 15px 0 0;
			}
			.shape{
				width: 56px;
				height: 11px;
				background-repeat: no-repeat;
				background-image: url(../img/shape-blue.png);
			}
		}
	}
	.panel-body{
		border: 1px solid $border-color;
		border-radius: 6px;
	}
	.panel-form{
		padding: 0;
		border: none;
		.form-group{
			margin: 0 0 20px 0;
		}
	}
}

.blog-single{
	img{
		border-radius: 8px;
		margin-bottom: 35px;
		width: 100%;
	}
	h2{
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
		font-size: 22px;
		font-weight: 600;
		color: $title-color;
		margin: 0 0 15px 0;
	}
	.list-blog-single{
		margin: 0 0 25px 0;
		li{
			i.fa{
				color: $primary-color;
				margin: 0 5px 0 0;
			}
			a{
				color: $text-color;
			}
		}
	}
	p{
		line-height: 26px;
		margin: 0 0 35px 0;
	}
}
.blog-single-title{
	h3{
		font-size: 24px;
		color: $primary-color;
		display: flex;
		align-items: center;
		margin: 0 0 25px 0;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-blue.png);
		}
	}
}
.blog-comments{
	margin: 80px 0;
	.media-object{
		width: 75px;
		height: 75px;
		border-radius: 8px;
	}
	.media-heading{
		font-family: 'Open Sans', sans-serif;
		letter-spacing: 0;
		font-weight: 400;
		font-size: 16px;
		margin: 0 0 9px 0;
	}
	.media-body{
		padding-left: 30px;
		p{
			margin: 0 0 19px 0;
		}
		.reply-link{
			color: $primary-color;
			margin: 0 0 20px 0;
			display: block;
		}
	}
	.publish-date{
		font-size: 13px;
		i.fa{
			margin: 0 5px 20px 0;
		}
	}
}
.form-groping{
	margin-bottom: 25px;
	position: relative;
	i.fa{
		right: 33px;
	}
	.btn-main{
		width: 175px;
	}
}
.blog-form{
	.form-group{
		margin: 0 -15px;
	}
}
// blog List
.blog-list{
	padding: 100px 0 0 0;
	.card-list{
		background-color: $white-color;
		box-shadow: 10px 14px 14px rgba(#273143, .08);
		border-radius: 6px;
		margin-bottom: 40px;
		// cursor: pointer;
		@include transition(all 0.3s ease-in-out);
		display: block;
		height: auto;
		&:hover{
			box-shadow: 6px 6px 7px rgba(#273143, .12);
		}
		@include tablet{
			display: flex;
			justify-content: flex-start;
			height: 270px;
		}
	}
	.cart-list-img{
		display: block;
		float: none;
		border-radius: 6px 6px 0 0;
		overflow: hidden;
		position: relative;

		&:hover{
			img{
				transform: scale(1);
			}
		}
		@include tablet{
			display: inline-block;
			float: left;
			border-radius: 6px 0 0 6px;
		}
		img{
			max-width: unset;
			border-radius: 6px 6px 0 0;
			width: 420px;
			transition: transform 0.7s ease;
			transform: scale(1.2);

			@include tablet{
				border-radius: 6px 0 0 6px;
				width: 414px;
			}
			@include desktops{
			}
		}
		.sticker-round{
			width: 70px;
	    padding: 10px 0;
	    border-radius: 13px;
	    box-shadow: 0 0 1px rgba(0,0,0,0.5);
	    background-color: $color1;
	    display: block;
	    text-align: center;
	    position: absolute;
	    left: 20px;
	    top: 20px;
	    font-size: 16px;
	    line-height: 20px;
	    font-weight: 600;
	    color: #ffffff;
		}
	}
	.card-list-details{
		padding: 15px;
		width: 100%;
		float: none;
		align-self: center;
		@include tablet{
			padding: 0 15px;
			width: 75%;
	  	float: left;
		}
	  @include desktops{
			width: 86%;
	  	padding: 0 45px;
	  }
		h4{
			margin: 0 0 15px 0;
			text-transform: capitalize;
			font-weight: 600;
			font-family: 'Open Sans', sans-serif;
			color: $title-color;
			letter-spacing: 0;
			font-size: 22px;
		}
		.list-blog{
			margin: 0 0 25px 0;
			li{
				i.fa{
					color: $primary-color;
					margin: 0 5px 0 0;
				}
				a{
					color: $text-color;
				}
			}
		}

		p{
			margin: 0 0 15px 0;
		}
		.btn-main{
			align-self: flex-start;
			width: 160px;
			border-radius: 25px;
		}
	}

}
