/*================= 5. HOME ==================*/
@import 'header';
@import 'slider';
@import 'footer';


/*================= 5.1 NEWS SECTION ==================*/
.card-sm{
	margin-bottom: 30px;
	@include tablet{
		margin: 0;
	}
	&:hover{
		.card-photo{
			box-shadow: 4px 5px 5px rgba(#273143, .20);
			.img-mask{
				opacity: 1;
			}
		}
		.card-caption{
			h4{
				a{
					color: $primary-color;
				}
			}
		}
	}
	&.card-sm-space{
		margin: 15px 0;
	}
}
.card-photo{
	position: relative;
	@include transition(all 0.3s ease-in-out);
	box-shadow: 10px 14px 14px rgba(#273143, .12);
	border-radius: 6px;
	img{
		border-radius: 6px;
		width: 100%;
		@include tablet{
			width: auto;
		}
	}
	.img-mask{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		@include transition(all 0.3s ease-in-out);
		opacity: 0;
		a{
			width: 50px;
			height: 50px;
			display: block;
			border-radius: 100%;
			background-color: $white-color;
			text-align: center;
			line-height: 50px;
			color: $primary-color;
			&:hover{
				color: $primary-color;
			}
		}
	}
}
.card-caption{
	padding: 25px 0 0 0;
	h4{
		margin: 0;
		letter-spacing: 0;
		font-weight: 400;
		a{
			color: $title-color;
			font-family: 'Open Sans', sans-serif;
			font-size: 22px;
			margin: 0 0 11px 0;
			display: block;
			font-weight: 600;
		}
	}
}

/*================= 5.2 TESTIMONIALS SECTION ==================*/
.testimonial{
	margin-bottom: 30px;
	@include tablet{
		margin: 0;
	}
	.comments{
		padding: 37px;
		border-radius: 6px;
		background-color: $white-color;
		box-shadow: 6px 6px 7px rgba(#273143, .13);
		margin: 0 0 20px 0;
		p{
			margin: 0;
			font-size: 16px;
		}
	}
	.caption{
		padding-left: 37px;
		display: flex;
		align-items: center;
		img{
			border-radius: 100%;
			margin: 0 20px 0 0;
		}
		h4{
			margin: 0;
			font-size: 20px;
			font-family: 'Open Sans', sans-serif;
			color: $white-color;
			font-weight: 600;
			letter-spacing: 0;
			small{
				display: block;
				color: $color7;
				font-weight: 400;
				font-size: 14px;
				margin-top: 7px;
				opacity: .8;
			}
		}
	}
}

/*================= 5.3 TEACHERS SECTION ==================*/
.teachers{
	margin-bottom: 30px;
	@include tablet{
		margin: 0;
	}
	&:hover{
		.teachers-photo{
			box-shadow: 6px 6px 7px rgba(#273143, .13);
			.photo-mask{
				opacity: 1;
			}
		}
		.caption{
			h4{
				a{
					color: $primary-color;
				}
			}
		}
	}
	.caption{
		padding: 25px 0 0 0;
		h4{
			text-align: center;
			margin: 0;
			letter-spacing: 0;
			font-weight: 400;
			a{
				color: $title-color;
				font-family: 'Open Sans', sans-serif;
				font-size: 20px;
				margin: 0 0 11px 0;
				display: block;
				font-weight: 600;
			}
			small{
				display: block;
				color: $primary-color;
				font-weight: 400;
				font-size: 14px;
				margin-top: 7px;
			}
		}
	}
}
.teachers-photo{
	position: relative;
	@include transition(all 0.3s ease-in-out);
	box-shadow: 10px 14px 14px rgba(#273143, .12);
	border-radius: 6px;
	img{
		border-radius: 6px;
	}
	.photo-mask{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: flex;
    justify-content: center;
    align-items: flex-end;
		@include transition(all 0.3s ease-in-out);
		opacity: 0;
	}
}

/*================= 5.4 ABOUT SECTION ==================*/
.about-video{
	width: 100%;
	float: none;
	border-radius: 6px;
	box-shadow: 15px 15px 15px rgba(#273143, .13);
	margin: 0 0 25px 0;
	@include tablet{
		width: 95%;
		float: right;
		position: relative;
		margin: 0;
	}
	img{
		border-radius: 6px;
	}
	.video-button{
		position: absolute;
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
	}
}
.about-info{
	height: auto;
	display: block;
	@include tablet{
		height: 361px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	h2{
		font-size: 37px;
		color: $primary-color;
		display: flex;
		align-items: center;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-blue.png);
		}
	}
	h4{
		font-family: 'Open Sans', sans-serif;
		color: $title-color;
		font-size: 16px;
		letter-spacing: 0;
		line-height: 22px;
		font-weight: 600;
	}
	.btn-main{
		padding: 0 30px;
	}
}
.video-box{
	a{
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		iframe{
			width: 100%;
			height: 100%;
			border: none;
		}
	}
}
/*================= 5.5 CALL TO ACTION SECTION ==================*/
.call-to-action{
	width: 100%;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	background-position: center;
	background-attachment: fixed;
	color: #ffffff;
	padding:75px 0;
	.action-info{
		text-align: center;
		h2{
			font-size: 42px;
			color: $white-color;
			margin: 0 0 15px 0;
			line-height: 45px;
			@include tablet{
				line-height: 33px;
			}
		}
		p{
			margin: 0 0 37px 0;
			color: $white-color;
			line-height: 22px;
			font-size: 15px;
			@include tablet{
				line-height: 15px;
				font-size: 18px;
			}
		}
		.btn-main{
			padding: 0 30px;
			font-family: 'Open Sans', sans-serif;
			font-weight: 700;
		}
	}
}

/*================= 5.6 SERVICES SECTION ==================*/
.service-item{
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin: 40px 0;
}
.service-icon{
	width: 80px;
	height: 80px;
	border-radius: 100%;
	background-color: $white-color;
	box-shadow: 10px 8px 15px rgba($black-color, .1);
	display: inline-block;
	text-align: center;
	line-height: 80px;
	font-size: 23px;
	margin: 0 30px 0 0;
}
.service-info{
	display: inline-block;
	width: 80%;
	h4{
		font-family: 'Open Sans', sans-serif;
		font-size: 22px;
		letter-spacing: 0;
		font-weight: 600;
		margin: 0 0 15px 0;
	}
	p{
		margin: 0;
	}
}
.service-adjust{
	text-align: left;
	@include tablet{
		text-align: right;
	}
	.service-icon{
		order: 0;
		margin: 0 30px 0 0;
		@include tablet{
			order: 1;
			margin: 0 0 0 30px;
		}
	}
}

/*================= 5.7 CLASS SECTION ==================*/
.class-section{
	background-repeat: no-repeat;
	background-size: 100% 301px;
}
.card-lg{
	margin-bottom: 30px;
	box-shadow: 10px 14px 14px rgba(#273143, .12);
	@include transition(all 0.3s ease-in-out);
	border-radius: 6px;
	@include tablet{
		margin: 0;
	}
	&:hover{
		box-shadow: 6px 6px 7px rgba(#273143, .12);
	}
}
.card-image{
	position: relative;
	border-radius: 6px 6px 0 0;
	img{
		width: 100%;
		border-radius: 6px 6px 0 0;
	}
}
.card-mask{
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-end;
	justify-content: center;
	&:hover{
		.btn-main{
			width: 160px;
			span{
				opacity: 1;
			}
		}
	}
	.btn-main{
		display: inline-block;
		width: 50px;
		height: 50px;
		line-height: 50px;
		padding: 0 0 0 14px;
		border-radius: 50px;
		background-color: $primary-color;
		overflow: hidden;
		text-align: left;
		color: $white-color;
		z-index: 100;
		margin-bottom: -15px;
		@include transition(all 0.3s cubic-bezier(.62,.1,.5,1));
		i.fa{
			color: $white-color;
		}
		span{
			height: 50px;
			white-space: nowrap;
			opacity: 0;
			color: $white-color;
		}
	}
}
.card-info{
	border-radius: 0 0 6px 6px;
	text-align: center;
	padding: 50px 0 0 0;
	h4{
		font-family: 'Open Sans', sans-serif;
		font-size: 22px;
		color: $title-color;
		letter-spacing: 0;
		font-weight: 600;
		margin: 0 0 12px 0;
	}
	p{
		padding: 0 15px;
		@include tablet{
			padding: 0 20px;
		}
	}
}
.wave-part{
	position: absolute;
	left: 0;
	bottom: 0;
	width: 100%;
}
/*================= 5.8 GALLERY SECTION ==================*/
.gallery-section{
	background-color: #f8f8fa;
}
.fancybox-overlay{
	background-image: url(../img/fancybox_overlay.png);
}
.fancybox-nav{
	background-image: none;
}
.gallery-photo{
	.overlay{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black-color, .5);
		border-radius: 6px;
		display: flex;
		justify-content: center;
		align-items: center;
		opacity: 0;
		@include transition(all 0.3s ease-in-out);
	}
	.btn-main{
		border-radius: 25px;
		padding: 0 20px;
	}
	&:hover{
		.overlay{
			opacity: 1;
		}
	}
}
.owl-carousel{
	&.gallery-slider{
		height: auto;
		padding-bottom: 105px;
	}
}
.owl-nav{
	top: 100%;
	margin-top: -55px;
	right: auto;
	width: auto;
	left: 50%;
	margin-left: -60px;
	opacity: 1;
}
.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot{
	border-radius: 100%;
	box-shadow: 6px 6px 7px rgba(#273143, .12);
}
.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next{
	margin: 0 5px;
	padding: 0;
	width: 55px;
	height: 55px;
	line-height: 55px;
	text-align: center;
}
.owl-prev span,
.owl-prev:hover span,
.owl-next span,
.owl-next:hover span{
	background-image: none;
}
.owl-prev span{
	width: 25px;
	height: 25px;
	margin: 0 auto;
	&:after{
    content: "\f104";
    font-family: FontAwesome;
    color: $primary-color;
    font-size: 25px;
	}
}
.owl-next span{
	width: 25px;
	height: 25px;
	margin: 0 auto;
	&:after{
    content: "\f105";
    font-family: FontAwesome;
    color: $primary-color;
    font-size: 25px;
	}
}
.owl-prev:hover span,
.owl-next:hover span{
	width: 25px;
	height: 25px;
}

/*================= 5.9 BANNER SECTION ==================*/
