  p {
    font-size: 13px;
    line-height: 23px;
    margin-bottom: 25px;
  }
}

@media (min-width: 768px) {
  p {
    font-size: 14px;
    line-height: 26px;
    margin-bottom: 25px;
  }
}

h1, h2, h3, h4, h5 {
