/*=== 2. UI KIT ===*/
/*=== 2.1 LIST ITEM ===*/
.list-address{
	li{
		margin: 0 0 15px 0;
		color: #c9d6df;
		i.fa{
			width: 29px;
			text-align: left;
			color: $primary-color;
		}
		a{
			color: #c9d6df;
			text-decoration: none;
		}
	}
}
.list-link{
	li{
		margin: 0 0 15px 0;
		a{
			color: #c9d6df;
		}
		i.fa{
			color: #c9d6df;
			margin: 0 10px 0 0;
		}
	}
}
.list-social{
	text-align: center;
	margin: 0 0 25px 0;
	@include tablet{
		text-align: right;
		margin: 0;
	}
	li{
		padding: 0 20px;
		a{
			font-size: 14px;
			color: $white-color;
			&:hover{
				color: $primary-color;
			}
		}
	}
}
.list-card{
	li{
		i.fa{
			font-size: 14px;
			color: $primary-color;
			margin: 0 5px 0 0;
		}
		a{
			color: $text-color;
			font-size: 14px;
			&:hover{
				color: $text-color;
			}
		}
	}
}
.list-teacher-social{
	padding: 17px;
	border-radius: 50px;
	background-color: $primary-color;
	margin: 0 0 30px 0;
	box-shadow: 15px 15px 15px rgba(#273143, .13);
	li{
		border-right: 1px solid rgba($white-color, .3);
		&:last-child{
			border: none;
		}
		a{
			color: $white-color;
			padding: 0 10px;
		}
	}
}
.list-card-item{
	display: flex;
	justify-content: space-between;
	padding: 15px 0;
	border-top: 1px solid $border-color;
	li{
		width: 33.33%;
		border-right: 1px solid $border-color;
		&:last-child{
			border: none;
		}
	}
	.card-price{
		font-weight: 600;
		color: $primary-color;
		font-size: 18px;
	}
}
.list-sidebar{
	margin: 0;
	padding: 0;
	@include desktops{
		padding: 0 15px;
	}
	li{
		border: none;
		border-bottom: 1px solid $border-color;
		padding: 20px 0;
		margin: 0;
		display: block;
		color: $title-color;
		font-size: 15px;
		text-transform: capitalize;
		a{
			color: $title-color;
		}
		&:last-child{
			border-bottom: none;
		}

		span{
			color: $text-color;
			font-size: 14px;
		}
		i.fa{
			width: 33px;
		}
	}
}
.list-panel{
	li{
		padding: 15px 0;
		a{
			display: flex;
			justify-content: flex-start;
			align-items: center;
			&:hover{
				.list-title-info{
					color: $primary-color;
					small{
						color: $text-color;
					}
				}
			}
		}
	}
}
.list-image{
	margin: 0 20px 0 0;
	border-radius: 6px;
	img{
		border-radius: 6px;
	}
}
.list-title-info{
	color: $title-color;
	small{
		display: block;
		font-size: 12px;
		color: $text-color;
		margin-top: 5px;
	}
}
/*=== 2.2 FORM ===*/
.form-footer{
	.form-control{
		background-color: rgba($white-color, .07);
		padding-left: 20px;
		border-radius: 0 10px;
		color: #c9d6df;
		&::-webkit-input-placeholder {
			color: #c9d6df;
		}
		&::-moz-placeholder {
			color: #c9d6df;
		}
		&:-ms-input-placeholder {
			color: #c9d6df;
		}
		&:-moz-placeholder {
			color: #c9d6df;
		}
	}
	.form-group{
		margin: 0 0 20px 0;
	}
}
.search-field{
	display: flex;
	justify-content: space-between;
	padding: 10px 25px;
	border-radius: 25px;
	background-color: $white-color;
	box-shadow: 4px 8px 14px rgba(#273143, .1);
	height: 50px;
	margin-bottom: 50px;
	.form-control{
		border: none;
		box-shadow: none;
		height: 34px;
		padding: 0;
		font-size: 14px;
	}
	.input-group-addon{
		background-color: transparent;
		border: none;
		color: $primary-color;
		font-size: 15px;
	}
}
.form-style{
	.form-group{
		position: relative;
		margin-bottom: 0;
	}
	i.fa{
		position: absolute;
		top: 18px;
		right: 18px;
	}
	.form-control{
		border: 1px solid $border-color;
		border-radius: 10px;
		box-shadow: none;
		padding: 10px 36px 10px 18px;
		font-size: 14px;
		color: $text-color;
	}
	textarea.form-control{
		height: 190px;
	}
	.btn-main{
		width: 175px;
	}
}
.form-field-wrapper{
  display: flex;
  margin: 0 0 30px 0;
}
.form-inner{
	flex: 1;
	display: flex;
	flex-direction: column;
  justify-content: space-between;
}
.formPanel{
	&.panel{
		border: 0;
		margin-bottom: 30px;
		@include border-radius(7px);
		.panel-heading{
			@include border-radius(7px 7px 0 0);
			.panel-title{
				font-size: 24px;
				color: $white-color;
				text-transform: capitalize;
				font-weight: 700;
				font-family: 'Dosis', sans-serif;
				letter-spacing: 0;
			}
		}
		.panel-body{
			@include add-border(1px, #dedede, all );
			border-top: 0;
			@include border-radius(0 0 7px 7px);
			.media-list{
				li{
					padding: 10px 0;
					margin: 0;
					.media-left.iconContent{
						width: 45px;
						height: 45px;
						line-height: 45px;
						text-align: center;
						color: $white-color;
						@include border-radius(100%);
						display: inline-block;
						float: left;
						padding: 0;
						margin-right: 20px;
						font-size: 16px;
					}
					.media-body.iconContent{
						.media-heading{
							font-family: 'Dosis', sans-serif;
							font-size: 20px;
							font-weight: 700;
						}
						p{
							font-weight: 600;
							margin-bottom: 0;
						}
					}
				}
				&.blogListing{
					li{
						.media-heading{
							a{
								font-weight: 600;
								font-size: 14px;
								text-transform: capitalize;
								color: $text-color;
								&:hover{
									color: $color5 !important;
								}
							}
						}
					}
				}
			}
		}
	}
}
.formField{
	.form-control{
		height: 50px;
		@include border-radius(0 10px);
		color: #222222;
		border: 1px solid  #e4e4e4;
		box-shadow: none;
		margin-bottom: 15px;
	}
	.btn-primary{
		margin-bottom: 15px;
		padding: 0;
		&:focus{
			outline: 0;
			@include box-shadow(0, 4px, 0, rgba($black-color, .1) );
		}
	}
	.help-block{
		text-align: center;
		margin-bottom: 0;
		a{
			&:hover{
				color: $color3;
			}
		}
	}
}
/*=== 2.3 BUTTON ===*/
.btn{
	font-family: 'Londrina Solid', cursive;
	height: 50px;
	line-height: 50px;
	text-transform: uppercase;
	letter-spacing: 2px;
	border-radius: 0 10px;
	font-size: 18px;
	&:focus, &:active:focus{
		outline: 0;
		box-shadow: none;
	}
}
.btn-main{
	padding: 0;
	color: $white-color;
	background-color: $primary-color;
	&:hover, &:focus, &:active:focus{
		color: $white-color;
	}
}
.btn-shadow{
	box-shadow: 10px 8px 15px rgba($black-color, .1);
	@include transition(all 0.3s ease-in-out);
	&:hover, &:focus, &:active:focus{
		box-shadow: 5px 5px 8px rgba($black-color, 0.1);
	}
}
.backToTop{
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 50px;
	height: 50px;
	text-align: center;
	line-height: 45px;
	background-color: $color1;
	border-radius: 0 6px;
	color: $white-color;
	display: block;
	font-size: 16px;
	z-index: 100;
	@include opacity(0);
	@include transition(all 0.3s ease-in-out);
	&:hover, &:focus, &:active{
		color: $white-color;
	}
}
.btn-margin{
	margin: 0 0 50px 0;
}
/*=== 2.4 PAGE TITLE ===*/
.page-title-section{
	background-image: url(../img/background/bg-page-title.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	padding: 50px 0;
	@include tablet{
		padding: 83px 0;
	}
}
.page-title{
	h2{
		font-size: 37px;
		color: $white-color;
		display: flex;
		align-items: center;
		margin: 0;
		.title-about{
			padding: 0 15px 0 0;
		}
		.shape{
			width: 56px;
			height: 11px;
			background-repeat: no-repeat;
			background-image: url(../img/shape-yellow.png);
		}
	}
}
.breadcrumb-custom{
	margin-bottom: 0;
	background-color: transparent;
	text-align: right;
  padding: 6px 15px;
	display: none;
	@include tablet{
		display: block;
	}
	li{
		a, &.active{
			text-transform: capitalize;
			color: $white-color;
		}
		&+li:before{
			content: "\f105";
			font-family: FontAwesome;
			color: $white-color;
		}
	}
}

/*=== 2.5 PAGINATION ===*/
.pagination-section{
	padding: 35px 0 100px 0;
}
.pagination-area{
	.pagination{
		text-align: center;
		width: 100%;
		margin: 0;
		li{
			display: inline-block;
			padding: 0 5px;
			&:first-child, &:last-child{
				a{
					border-radius: 100%;
					font-size: 22px;
					line-height: 35px;
					@include tablet{
						line-height: 45px;
					}
				}
			}
			&.active{
				a{
					background-color: $primary-color;
					color: $white-color;
					border-color: $primary-color;
					box-shadow: 10px 10px 10px rgba($black-color, .12);
				}
			}
		}
		a{
			width: 40px;
			height: 40px;
			border-radius: 100%;
			line-height: 39px;
			text-align: center;
			padding: 0;
			font-size: 16px;
			font-weight: 600;
			color: $text-color;
			border-color: $border-color;
			@include tablet{
				width: 50px;
				height: 50px;
				line-height: 50px;
			}
			&:hover, &:focus, &:focus:hover{
				background-color: $primary-color;
				color: $white-color;
				border-color: $primary-color;
				box-shadow: 10px 10px 10px rgba($black-color, .12);
			}
		}
	}
}
/*=== 2.6 ACCORDION ===*/
.accordion-common{
	.panel{
		margin-bottom: 30px;
		border: none;
	}
	.panel-heading{
		display: block;
		padding: 14px 20px;
		border-radius: 8px;
		font-weight: 600;
		background-color: $white-color;
		border: 1px solid $border-color;
		color: $title-color;
		text-transform: capitalize;
		position: relative;
		font-family: 'Open Sans', sans-serif;
		@include mobile-xs{
			font-size: 18px;
		}
		@include mobile{
			font-size: 20px;
		}
		span.iconBlock{
			width: 20px;
			height: 20px;
			line-height: 20px;
			text-align: center;
			background-color: transparent;
			color: $text-color;
			display: inline-block;
			float: right;
			&.iconTransparent{
				background-color: transparent;
				box-shadow: none;
				color: $white-color;
			}
		}
		&.color-2{
			background-color: $color2;
			color: $white-color !important;
			border-color: $color2;
			span.iconBlock{
				color: $white-color;
			}
		}
		&.color-1{
			background-color: $color1;
			color: $white-color !important;
			border-color: $color1;
			span.iconBlock{
				color: $white-color;
			}
		}
		&.color-6{
			background-color: $color6;
			color: $white-color !important;
			border-color: $color6;
			span.iconBlock{
				color: $white-color;
			}
		}
		&.color-4{
			background-color: $color4;
			color: $white-color !important;
			border-color: $color4;
			span.iconBlock{
				color: $white-color;
			}
		}
		&.color-5{
			background-color: $color5;
			color: $white-color !important;
			border-color: $color5;
			span.iconBlock{
				color: $white-color;
			}
		}
		&.collapsed, &.defult-color{
			&.color-2{
				background-color: $white-color;
				color: $color2 !important;
				border-color: $border-color;
				span.iconBlock{
					color: $text-color;
				}
			}
			&.color-1{
				background-color: $white-color;
				color: $color1 !important;
				border-color: $border-color;
				span.iconBlock{
					color: $text-color;
				}
			}
			&.color-6{
				background-color: $white-color;
				color: $color6 !important;
				border-color: $border-color;
				span.iconBlock{
					color: $text-color;
				}
			}
			&.color-4{
				background-color: $white-color;
				color: $color4 !important;
				border-color: $border-color;
				span.iconBlock{
					color: $text-color;
				}
			}
			&.color-5{
				background-color: $white-color;
				color: $color5 !important;
				border-color: $border-color;
				span.iconBlock{
					color: $text-color;
				}
			}
		}
	}
	span.separator{
		width: 1px;
		height: 100%;
		background-color: rgba($white-color, .25);
		position: absolute;
		top: 0;
		right: 60px;
	}
	.panel-body{
		padding: 30px;
		background-color: $white-color;
		box-shadow: 6px 6px 7px rgba(#273143, .13);
		border-top: none !important;
		@include border-radius( 0 0 8px 8px);
		p{
			margin-bottom: 35px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}

/*=== 2.7 RADIO BUTTON ===*/
.radio-single {
  margin:0 0 15px 0;
  input[type="radio"] {
  	display:none;
  }
  input[type="radio"] + label {
  	color: $text-color;
  	font-size:14px;
  	font-weight: 400;
  	margin: 0;
  }
  input[type="radio"] + label span {
  	display:inline-block;
  	width:12px;
  	height:12px;
  	margin:-1px 10px 0 0;
  	vertical-align:middle;
  	cursor:pointer;
  	-moz-border-radius:  50%;
  	border-radius:  50%;
  	border: 1px solid $text-color;
  	background-color: $white-color;
  }

  input[type="radio"] + label span {
  	background-color: $white-color;
  }

  input[type="radio"]:checked + label span{
  	background-color: $primary-color;
  	border-color: $primary-color;
  }

  input[type="radio"] + label span,
  input[type="radio"]:checked + label span {
  	@include transition(all 0.3s ease-in-out);
  }
}

/*=== 2.8 RANGE SLIDER ===*/
.price-range{
	.progress{
		margin: 20px 0 25px 0;
		height: 4px;
		background-color: #252525;
		position: relative;
		overflow: visible;
		.progress-bar{
			background-color: $color2 !important;
		}
		.bulet{
			width: 16px;
			height: 16px;
			background-color: $color2 !important;
			border: 2px solid $color2 !important;
			@include border-radius(100%);
			border-radius: 100%;
			left: 0px;
			top: -5px;
			position: absolute;
			&.next{
				left: 60px;
			}
		}
	}
	.priceLabel{
		float: right;
		line-height: 30px;
		color: $secondary-color;
		@include mobile{
			font-size: 14px;
		}
		@include tablet{
			font-size: 5px;
		}
		@include desktops{
			font-size: 14px;
		}
	}
}
.price-range{
	position: relative;
	display: inline-block;
	width: 100%;
	float: left;
	margin-top: 20px;
	.price-slider-inner{
		display: block;
		margin: 15px 0 25px 0;
		.amount-wrapper{
			position: absolute;
			bottom: 14px;
			left:0;
			font-size: 14px;
			width: 130px;
			color: #666666;
			input{
				background-color: transparent;
				border: 0;
				width: 28%;
				font-size: 14px;
				color: $text-color;
				font-weight: 400;
				text-align: right;
			}
		}
		.ui-widget-content{
			background: none;
			border: 0;
			background-color: #e5e5e5;
			height: 5px;
			clear: both;
			border-radius: 0;
			@include border-radius(2px);
			margin: 0 5px 0 9px;
			.ui-slider-range{
				background-color: $primary-color;
				border: none;
				border-radius: 0;
			}
			.ui-slider-handle{
				border-radius: 100%;
				background: none;
				border: 2px solid $primary-color;
				background-color: $white-color;
				top: -7px;
				width: 19px;
				height: 19px;
				outline: none;
				box-shadow: 0 3px 7px rgba(#273143, .19);
			}
		}
	}
	.btn-main{
		@include mobile-xs{
			padding: 0 21px;
		}
		@include tablet{
			padding: 0 16px;
		}
		@include desktops{
			padding: 0 21px;
		}

	}
}
