/*=== 3. HEADER ===*/
.navbar .container{
	position:relative;
}
.bb-fixed-header {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 999;
}
/*=== 3.1 TOP COLOR BAR ===*/
.color-bar{
	width: 100%;
	height: 4px;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f0c24b+0,b5d56a+20,ea7066+40,84bed6+60,a597e7+80,ea77ad+100 */
	background: #f0c24b; /* Old browsers */
	background: -moz-linear-gradient(left, #f0c24b 0%, #b5d56a 20%, #ea7066 40%, #84bed6 60%, #a597e7 80%, #ea77ad 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(left, #f0c24b 0%,#b5d56a 20%,#ea7066 40%,#84bed6 60%,#a597e7 80%,#ea77ad 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, #f0c24b 0%,#b5d56a 20%,#ea7066 40%,#84bed6 60%,#a597e7 80%,#ea77ad 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0c24b', endColorstr='#ea77ad',GradientType=1 ); /* IE6-9 */
	.col-sm-1{
		height: 3px;
		text-indent: -99999px;
	}
}
.top-fixed{
	position: fixed;
	z-index: 2000;
}

/*=== 3.2 TOP INFO BAR ===*/
.top-info-bar{
	padding: 15px 0 12px 0;
	display: block;
	width: 100%;
	z-index: 1000;
	position: relative;
	top: 3px;
	@include transition(height 0.15s ease);
	height:53px;
}

/*=== 3.3 HEADER TRANSPARENT ===*/
.navbar-default{
	background-color: transparent;
	border: none;
	border-radius: 0;
	margin-bottom: 0;
	z-index:700;
}
.navbar-header .navbar-brand {
  float: left;
  height: auto;
  padding: 15px;
  width: 160px;
  > img{
  	width: 100%;
  }
  @include tablet{
  	height: auto;
    padding: 18px 0 18px 15px;
    width: 180px;
  }
}
.navbar-default .navbar-nav{
	background-color: $white-color;
	margin-bottom:0;
	@include mobile-xs{
		margin-top: 0;
	}
	@include tablet{
		height:auto;
	}
	&.navbar-right{
		margin-right: -15px;
		@include tablet{
			margin-right: 50px;
			// margin-top: 5px;
		}
	}
	>li{
		>a{
			font-weight: 600;
			font-size: 15px;
			color: $black-color;
			padding: 12px;
			@include tablet{
				margin: 0;
				padding: 23px 0;
			}
			> span{
				padding: 0;
				line-height: 16px;
				text-align: center;
				text-transform: uppercase;
				display: inline-block;
				@include tablet{
					width: auto;
					padding: 0 15px;
					display: block;
				}
				@include desktops{
					width: auto;
					padding: 0 18px;
				}
			}
		}
		&:last-child{
			>a{
				>span{
					width: auto;
					@include tablet{
						padding-left: 15px;
					}
				}
			}
		}
		&.active{
			>a, >a:hover, >a:focus{
				background-color: $color1;
				color: $white-color;
				@include opacity(1);
			}
		}
		&.open{
			>a, >a:hover > a:focus{
				background-color: $color1;
				color: $white-color;
			}
		}
		&.dropdown{
			.dropdown-menu{
				a{
					padding: 3px 20px;
					margin-bottom: 0;
				}
			}
		}
	}
}
.navbar-nav > li.dropdown.open > a:after{
	@include mobile-xs{
		display: none;
	}
	@include tablet{
		border-left: 9px solid transparent;
	    border-right: 9px solid transparent;
	    margin-left: -10px;
	    content: "";
	    display: inline-block;
	    left: 50%;
	    position: absolute;
	    top: 102px;
	}
}
.navbar-nav > li.color-1.dropdown  a:after{
	border: none;
}
.navbar-nav > li.color-2.dropdown.megaDropMenu > a:after{
	border: none;
}
.navbar-nav > li.color-3.dropdown  a:after{
	border: none;
}
.navbar-nav > li.color-4.dropdown  a:after{
	border: none;
}
.navbar-nav > li.color-5.dropdown  a:after{
	border: none;
}
.navbar-nav > li.color-6.dropdown  a:after{
	border: none;
}
.navbar-nav > li.dropdown.singleDrop .dropdown-menu .dropdown-menu:after {
	@include mobile-xs{
		display: none;
	}
	@include tablet{
		// @include add-border(8px, transparent, top-bottom);
		top: 20%;
		margin-top: -10px;
		content: "";
		display: inline-block;
		left: -11px;
		position: absolute;
	}
}
.navbar-nav > li.color-3.dropdown.singleDrop .dropdown-menu .dropdown-menu:after {
	border: none;
}
.navbar-nav > li.color-4.dropdown.singleDrop .dropdown-menu .dropdown-menu:after {
	border: none;
}
.navbar-nav > li.color-5.dropdown.singleDrop .dropdown-menu .dropdown-menu:after {
	border: none;
}
.navbar-nav > li.color-6.dropdown.singleDrop .dropdown-menu .dropdown-menu:after {
	border: none;
}
.navbar-nav > li.color-1.singleDrop > .dropdown-menu li.active a{
	background-color:transparent;
}
.navbar-nav > li.color-1.singleDrop > .dropdown-menu li.active a:hover{
	background-color:$color1;
}
.navbar-nav > li.dropdown.megaDropMenu{
	position: static;
	.dropdown-menu{
		margin: -2px auto 0 auto;
		right: 15px;
		@include border-radius(5px);
		@include mobile-xs{
			left: 0;
			padding: 0;
			width: 100%;
		}
		@include tablet{
			left: 15px;
			padding: 10px 5px 0;
			width: 720px;
		}
		@include desktops{
			width: 940px;
		}
		@include large-desktops{
			width: 1140px;
		}
		.list-unstyled{
			width: 100%;
			float: left;
			@include mobile-xs{
				padding-bottom: 5px;
			}
			@include tablet{
				padding-bottom: 15px;
			}
			li{
				&.listHeading, &:first-child{
					float: left;
					font-weight: 700;
					font-family: 'Londrina Solid', cursive;
					@include mobile-xs{
						padding: 10px 12px 10px 0;
						font-size: 20px;
					}
					@include tablet{
						padding: 10px 12px;
						font-size: 24px;
					}
				}
				a{
					color: $text-color;
					font-size: 14px;
					float: left;
					width: 100%;
					background-color: transparent;
					@include mobile-xs{
						padding: 6px 12px;
					}
					@include tablet{
						padding: 10px 12px;
					}
					&:hover{
						padding-left: 22px;
					}
				}
			}
		}
	}
	&.open > a > span{
		color: $white-color ;
	}
}
.navbar-nav > li.dropdown.singleDrop{
	.dropdown-menu{
		@include border-radius(5px);
		margin-top: 0;
		padding: 10px;
		@include mobile-xs{
			min-width: 230px;
			margin-top: -20px;
		}
		@include tablet{
			min-width: 210px;
			margin-top: 0px;
		}
		@include large-desktops{
			min-width: 230px;
			margin-top: 0px;
		}
		li{
			a{
				color: $text-color;
				font-size: 14px;
				float: none;
				width: 100%;
				background-color: transparent;
				@include mobile-xs{
					padding: 6px 12px 6px 24px;
				}
				@include tablet{
					padding: 10px 12px;
				}
				&:hover{
					padding-left: 22px;
				}
			}
			.dropdown-menu{
				margin-left: 0;
				li{
					a{
						@include mobile-xs{
							padding: 6px 12px 6px 36px;
						}
						@include tablet{
							padding: 10px 12px;
						}
						&:hover{
							padding-left: 22px;
						}
					}
				}
			}
		}
	}
}
.dropdown-submenu{
	position:relative;
	a{
		position: relative;
		i{
			position: absolute;
			right: 10px;
			top: 13px;
		}
	}
	>.dropdown-menu{
		top:0;
		margin-top: 0 !important;
		margin-left:-1px;
		-webkit-border-radius:0 6px 6px 6px;
		-moz-border-radius:0 6px 6px 6px;
		border-radius:0 6px 6px 6px;
		@include mobile{
			left: 0;
		}
		@include tablet{
			left: 100%;
		}
	}
	&.pull-left{
		float:none;
		>.dropdown-menu{
			left:-100%;
			margin-left:10px;
			-webkit-border-radius:6px 0 6px 6px;
			-moz-border-radius:6px 0 6px 6px;
			border-radius:6px 0 6px 6px;
		}
	}
}
.navbar-toggle{
	margin-top: 20px;
}
.navbar-toggle{
	background-color: $white-color;
}
.navbar-default .navbar-nav{
    height: 340px;
    overflow-x:hidden;
    @include transition(height 0.3s ease);
    @include tablet{
    	height: auto;
    	 overflow-x:visible;
    	  overflow-y:visible;
	}
}
.navbar-collapse.in {
    overflow-y: visible;
}
.navbar-collapse{
	@include mobile-xs{
		@include box-shadow(0, 4px, 0, rgba($black-color, .1) );
	}
	@include tablet{
		border-color: $white-color;
		box-shadow:none;
	}
	li{
		&.dropdown{
			.dropdown-menu{
				box-shadow:none;
				@include mobile-xs{
					position: relative;
					width: 100%;
					float: left;
					border: 0;
					display: block;
				}
				@include tablet{
					position: absolute;
					float: left;
					display: none;
					-webkit-box-shadow: 0 4px 6px rgba(0,0,0,.175);
    				box-shadow: 0 4px 6px rgba(0,0,0,.175);
				}
			}
			&.open{
				.dropdown-menu{
					display: block;
				}
			}
			&.dropdown-submenu{
				.dropdown-menu{
					@include mobile-xs{
						position: relative;
						width: 100%;
						float: left;
						border: 0;
						display: block;
					}
					@include tablet{
						position: absolute;
						float: left;
						display: none;
					}
				}
				&.open{
					.dropdown-menu{
						display: block;
					}
				}
			}
		}
	}
}

/*=== 3.4 LIGHT HEADER ===*/
.navbar-default.lightHeader{
	@include transition(all 0.3s ease-in-out);
	background-color: $white-color;
	height: 78px;
	top: 0;
	@include tablet{
		height: 100px;
	}
	&:after{
		background-image: url("../img/shape.png");
		height: 9px;
		background-repeat: repeat-x;
		position: absolute;
		left: 0;
		bottom: -8px;
		width: 100%;
	}
	.navbar-nav{
		> li > a{
			color: $black-color;
			font-family: 'Londrina Solid', cursive;
			letter-spacing: 2px;
			font-weight: 400;
			font-size: 18px;
			padding: 20px 0 20px 15px;
			border-radius: 0;
			margin: 0;
			@include tablet{
				font-size: 13px;
				margin: -4px 0 0 0;
				border-radius: 0 0 50% 50%;
				padding: 45px 0 25px;
			}
			@include desktops{
				font-size: 18px;
				margin: -4px 0 0 0;
				border-radius: 0 0 50% 50%;
				padding: 45px 0 30px;
			}
			&:hover{
				margin: 0;
				@include tablet{
					margin: -4px 0 0 0;
				}
			}
		}
		>li.active > a{
			color: $white-color;
			margin: 0;
			@include tablet{
				margin: -4px 0 0 0;
			}
			span{
				color: $white-color;
			}
		}
		>li.active.color-1 > a{
			background-color: $color1;
		}
		>li.active.color-2 > a{
			background-color: $color2;
		}
		>li.active.color-3 > a{
			background-color: $color3;
		}
		>li.active.color-4 > a{
			background-color: $color4;
		}
		>li.active.color-5 > a{
			background-color: $color5;
		}
		>li.active.color-6 > a{
			background-color: $color6;
		}
	}
}
