footer{
	position: relative;
	width: 100%;
	display: block;
	padding: 0;
} 

/*=== 4.2 FOOTER INFO ===*/
.footer-info{
	padding: 100px 0;
	background-color: $secondary-color;
	position: relative;
	&:after{
		background-image: url("../img/shape.png");
		height: 9px;
		background-repeat: repeat-x;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
	}	
}
.footer-inner{
	margin: 0 0 25px 0;
	@include tablet{
		margin: 0;
	}
	.footer-logo{
		display: block;
		margin-bottom: 27px;
	}
	p{
		color: #c9d6df;
		line-height: 26px;
	}
	h4{
		font-size: 22px;
		color: $white-color;
		margin: 0 0 45px 0; 
		text-transform: uppercase;
	}
}
/*=== 4.2 COPY RIGHT ===*/
.copyRight{
	padding: 23px 0;
	background-color: #1e282f;
	p{
		margin: 0;
		color: #c9d6df;
		text-align: center;
		@include tablet{
			text-align: left;
		}
	}
}
