}

.form-control{
	border: none;
	height: 50px;
	font-size: 13px;
	color: $text-color;
	border-radius: 6px;
	&::-webkit-input-placeholder {
		color: $text-color;
	}
	&::-moz-placeholder {
		color: $text-color;
	}
	&:-ms-input-placeholder {
		color: $text-color;
	}
	&:-moz-placeholder {
		color: $text-color;
	}
	&:focus{
		box-shadow: none;
		border-color: transparent;
	}
}
::selection {
  background-color: $primary-color;
  color: $white-color;
}
