section, footer{
	display: block;
	width: 100%;
	float: left;
	padding: 50px 0;
	background-color: $white-color;
	@include tablet{
		padding: 100px 0;
	}
}
.section-title{
	text-align: center;
	margin: 0 0 50px 0;
	h2{
		margin: 0;
		color: $primary-color;
		font-size: 37px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.title-text{
		padding: 0 15px;
	}
	.shape{
		width: 56px;
		height: 11px;
		background-repeat: no-repeat;
		background-image: url(../img/shape-blue.png);
	}
	&.section-title-alt{
		h2{
			color: #fff;
		}
		.shape{
			background-image: url(../img/shape-blue-light.png);
		}
	}
	&.section-space{
		margin: 0 0 35px 0;
	}
}
.shape-roatet{
	-webkit-transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	-moz-transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	-o-transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	-ms-transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	transform: rotateX(0deg) rotateY(180deg) rotateZ(0deg);
}
aside{
	width: 100%;
	@include desktops{
		width: 95%;
	}
	&.pull-right{
		float: left !important;
		@include tablet{
			float: right !important;
		}
	}
}
.p0{
	padding: 0 !important;
}
.m0{
